import { useQuery } from '@tanstack/react-query';
import { Badge, Col, Container, ListGroup, Row } from 'react-bootstrap';
import { API_MAP, request } from 'services/httpService';
import { WifiDetails } from '../components/WifiSelectModal/model/WfiDetails';
import { useWifiContext } from '../components/WifiSelectModal/WifiContext';
import { WifiSelectModal } from '../components/WifiSelectModal/WifiSelectModal';
import { GET_ALL_WIFI_DETAILS } from '../constants/queryKeys';

const WifiItem = (wifiDetails: WifiDetails) => {
  const wifiContext = useWifiContext();
  const clickOnItem = (wifiDetails: WifiDetails) => {
    wifiContext.setWfiDetails(wifiDetails);
    wifiContext.setShowWifiSelectModal(true);
  };

  return (
    <ListGroup.Item className="d-flex justify-content-between" onClick={() => clickOnItem(wifiDetails)}>
      {wifiDetails.ssid}
      <Badge className={wifiDetails.connected ? '' : 'invisible'} pill bg="success">
        V
      </Badge>
    </ListGroup.Item>
  );
};

export const WifiLayout = () => {
  const { data: wifiList } = useQuery(
    [GET_ALL_WIFI_DETAILS],
    async () => (await request(API_MAP.getWifiList)),
    {
      refetchInterval: 5000,
    }
  );

  return (
    <>
      <Container>
        <Row>
          <Col lg="5">
            <ListGroup>
              {wifiList?.map((wifiDetails: WifiDetails) => (
                <WifiItem key={wifiDetails.ssid} {...wifiDetails} />
              ))}
            </ListGroup>
          </Col>
        </Row>
      </Container>
      <WifiSelectModal />
    </>
  );
};
