import { useShowKeyboard } from 'hooks/useShowKeyboard';
import { useState, useEffect } from 'react';
import { Button, Form, Modal } from 'react-bootstrap';
import { API_MAP, request } from 'services/httpService';
import { WifiDetails } from './model/WfiDetails';
import { useWifiContext } from './WifiContext';

const connectToWifi = (wfiDetails: WifiDetails, password: string) => {
  request(API_MAP.connectToWifi, { payload: { ...wfiDetails, password } });
};

export const WifiSelectModal = () => {
  const wifiContext = useWifiContext();

  return (
    <>
      <Modal show={wifiContext.showWifiSelectModal} fullscreen={true}>
        <Modal.Body>
          <WifiFillPassword />
        </Modal.Body>
      </Modal>
    </>
  );
};

const WifiFillPassword = () => {
  const [password, setPassword] = useState('');
  const wifiContext = useWifiContext();
  const showKeyboard = useShowKeyboard();

  const hideWifiSelectModal = () => {
    wifiContext?.setShowWifiSelectModal(false);
  };

  return (
    <>
      <Form>
        <Form.Group className="mb-3">
          <Form.Label>SSID:</Form.Label>
          <Form.Control type="text" disabled value={wifiContext.wfiDetails.ssid} />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>Password:</Form.Label>
          <Form.Control
            type="password"
            placeholder="Password"
            onClick={(e) => {
              showKeyboard({ show: true, input: password, setInput: setPassword });
            }}
            defaultValue={password}
          />
        </Form.Group>
        <Button variant="primary" type="submit" onClick={() => connectToWifi(wifiContext.wfiDetails, password)}>
          Connect
        </Button>
        <Button variant="danger" type="submit" onClick={hideWifiSelectModal}>
          Cancel
        </Button>
      </Form>
    </>
  );
};
