import { GWSetupWizardSteps } from '../../types/GWSetupWizard.types';
import './GWSetupProgressBar.css';

export const GWSetupProgressBar = (props: { currentStep: GWSetupWizardSteps }) => {
  const { currentStep } = props;

  const getClassName = (liStep: number) => {
    return currentStep === liStep ? 'current-stage' : currentStep > liStep ? 'completed-stage' : '';
  };

  return (
    <div className="pb-container">
      <ul className="progressbar">
        <li className={getClassName(GWSetupWizardSteps.clinic)} step-ind={GWSetupWizardSteps.clinic}>
          Clinic
        </li>
        <li className={getClassName(GWSetupWizardSteps.location)} step-ind={GWSetupWizardSteps.location}>
          Location
        </li>
        <li className={getClassName(GWSetupWizardSteps.machines)} step-ind={GWSetupWizardSteps.machines}>
          Machines
        </li>
      </ul>
    </div>
  );
};
