import { WifiDetails } from 'components/WifiSelectModal/model/WfiDetails';

export const wifiList = (): Array<WifiDetails> => {
  const wifis: Array<WifiDetails> = [];
  for (let i = 0; i < 4; i++) {
    wifis.push({
      connected: i === 0,
      signal: 30,
      ssid: 'WIFI_' + Math.random(),
    });
  }
  return wifis;
};
