import React from 'react';
import { ProjectContact } from "pages/GWSetupWizard";
import { Col, Row } from "react-bootstrap";

export const ContactList = ({contacts} : {contacts: Array<ProjectContact>}) => {
    return (
      <>
        {contacts.map((contact, i) => (
          <React.Fragment key={i}>
            <Row>
              <Col className="d-flex">
                <span className="setup-label">Name</span>
                {contact.name}
              </Col>
            </Row>
            <Row>
              <Col className="d-flex">
                <span className="setup-label">Email</span>
                {contact.email}
              </Col>
            </Row>
            <Row>
              <Col className="d-flex">
                <span className="setup-label">Title</span>
                {contact.position}
              </Col>
            </Row>
            <Row>
              <Col className="d-flex">
                <span className="setup-label">Phone</span>
                {contact.phone}
              </Col>
            </Row>
            <hr />
          </React.Fragment>
        ))}
      </>
    );
  };