import { useAsync } from 'hooks/useAsync';
import { ProjectMetadata } from 'pages/GWSetupWizard';
import { cotService } from 'services/cot.service';
import { CotProject } from 'types/cot.types';

export const useGetProjectData = (
  companyId: string,
  projectId: string
): {
  projectDataStatus: 'idle' | 'pending' | 'success' | 'error';
  projectMetadata?: ProjectMetadata;
  project?: CotProject;
} => {
  const { value: projectData, status: projectDataStatus } = useAsync(() =>
    Promise.all([
      cotService.getProjectProperty<ProjectMetadata>(companyId!, projectId!, 'meta.data'),
      cotService.getProject(companyId!, projectId!),
    ])
  );
  if (!projectData) {
    return { projectDataStatus };
  }
  const [projectMetadata, project] = projectData!;
  return { projectDataStatus, projectMetadata, project };
};
