import arrowIcon from 'assets/images/arrow.svg';
import { Button, Col, Collapse, Container, Form, InputGroup, Row } from 'react-bootstrap';
import { useNavigate, useParams } from 'react-router';
import { useRef, useState, useEffect } from 'react';
import { GWSetupProgressBar } from '../components/GWSetupProgressBar/GWSetupProgressBar';
import { GWSetupWizardSteps } from '../types/GWSetupWizard.types';
import { cotService } from 'services/cot.service';
import { useSpinnerEffect } from 'hooks/useSpinner';
import { useAsync } from 'hooks/useAsync';
import { ProjectContact, ProjectMetadata } from '../GWSetupClinicSearch/GWSetupClinicSearch';
import { ClinicDetails } from 'components/ClinicDetails/ClinicDetails';
import { ContactList } from 'components/ContactList/ContactList';
import { routsConstant } from 'routes/routes.const';
import { useGetProjectData } from 'hooks/project/useGetProjectData';

const projectContactInitValue = (): ProjectContact => {
  return { name: '', phone: '', email: '', position: '' };
};

const ContactListCollapse = ({ contacts }: { contacts: Array<ProjectContact> }) => {
  const [openCollapseContactList, setOpenCollapseContactList] = useState(false);
  if (!contacts.length) {
    return <></>;
  }
  return (
    <>
      <Row>
        <Col>
          <Form.Group controlId="contactList">
            <Button
              variant="link"
              className="setup-add-link"
              onClick={() => setOpenCollapseContactList(!openCollapseContactList)}
              aria-expanded={openCollapseContactList}
            >
              +<span className="underline ms-1">Show list</span>
            </Button>
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col>
          <Collapse in={openCollapseContactList}>
            <div>
              <ContactList contacts={contacts}></ContactList>
            </div>
          </Collapse>
        </Col>
      </Row>
    </>
  );
};

const ContactPerson = ({ contacts }: { contacts: Array<ProjectContact> }) => {
  const [contactPerson, setContactPerson] = useState(() => projectContactInitValue());
  return (
    <>
      <Row>
        <Col className="setup-section-title mt-4">Contact Person</Col>
      </Row>
      <Row>
        <Col className="setup-comment mb-2">
          We will use this info to create a CoT user, please make sure it's accurate.
        </Col>
      </Row>
      <Row>
        <Col>
          <Form.Group className="mb-2" controlId="contactName">
            <Form.Control
              className="setup-form-field-text"
              type="text"
              placeholder="Name*"
              onChange={(e) => setContactPerson({ ...contactPerson, name: e.target.value })}
              value={contactPerson.name}
            />
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col>
          <Form.Group className="mb-2" controlId="contactEmail">
            <Form.Control
              className="setup-form-field-text"
              type="text"
              placeholder="Email*"
              onChange={(e) => setContactPerson({ ...contactPerson, email: e.target.value })}
              value={contactPerson.email}
            />
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col>
          <Form.Group className="mb-2" controlId="contactTitle">
            <Form.Control
              className="setup-form-field-text"
              type="text"
              placeholder="Title"
              onChange={(e) => setContactPerson({ ...contactPerson, position: e.target.value })}
              value={contactPerson.position}
            />
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col>
          <Form.Group controlId="phone">
            <InputGroup>
              <Form.Control
                className="setup-form-field-text"
                type="text"
                placeholder="Phone"
                onChange={(e) => setContactPerson({ ...contactPerson, phone: e.target.value })}
                value={contactPerson.phone}
              />
              <InputGroup.Text className="plus-icon">
                <img alt="add phone" src={arrowIcon} />
              </InputGroup.Text>
            </InputGroup>
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col>
          <Form.Group controlId="addContact">
            <Button
              variant="link"
              className="setup-add-link"
              onClick={() => {
                contacts.push(contactPerson);
                setContactPerson(projectContactInitValue());
              }}
            >
              +<span className="underline ms-1">Add another contact</span>
            </Button>
          </Form.Group>
        </Col>
      </Row>
      <ContactListCollapse contacts={contacts}></ContactListCollapse>
    </>
  );
};

export const GWSetupSetLocation = () => {
  const { projectId, companyId, deviceId } = useParams();
  const navigate = useNavigate();
  const projectMetadataRef = useRef<ProjectMetadata | undefined>(undefined);

  const { status: saveStatus, execute: submit } = useAsync<void>(() =>
    cotService.setProjectProperty<ProjectMetadata>(companyId!, projectId!, 'meta.data', projectMetadataRef.current!)
  );
  const { projectDataStatus, projectMetadata, project } = useGetProjectData(companyId!, projectId!);
  const dataLoadding = [projectDataStatus, saveStatus].includes('pending');
  useSpinnerEffect(dataLoadding);

  useEffect(() => {
    if (saveStatus === 'success') {
      navigate(
        routsConstant.gwSetupLocationDetails.relatirve({
          companyId: companyId!,
          projectId: projectId!,
          deviceId: deviceId!,
        })
      );
    }
  }, [saveStatus, companyId, projectId, deviceId, navigate]);

  if (projectDataStatus === 'pending') {
    return <></>;
  }
  projectMetadataRef.current ??= { ...projectMetadata!, contacts: projectMetadata?.contacts ?? [] };
  return (
    <>
      <GWSetupProgressBar currentStep={GWSetupWizardSteps.location} />
      <Container>
        <Form>
          <Row>
            <Col>
              <Button className="circle-button" onClick={() => console.log('back')}>
                <img alt="back" src={arrowIcon} />
              </Button>
            </Col>
          </Row>
          <Row>
            <Col className="setup-step-title mb-4">Set Gateway Location</Col>
          </Row>
          <ClinicDetails projectMetadata={projectMetadataRef.current} project={project}></ClinicDetails>
          <Row>
            <Col>
              <Form.Label className="setup-section-title mt-4">Where is the gateway installed?</Form.Label>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="gwFloor">
                <Form.Control className="setup-form-field-text" type="text" placeholder="Floor" />
              </Form.Group>
            </Col>
            <Col>
              <Form.Group className="mb-2" controlId="gwRoom">
                <Form.Control className="setup-form-field-text" type="text" placeholder="Room" />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="gwPhone">
                <InputGroup>
                  <Form.Control className="setup-form-field-text" type="text" placeholder="Phone*" />
                  <InputGroup.Text className="plus-icon">
                    <img alt="add phone" src={arrowIcon} />
                  </InputGroup.Text>
                </InputGroup>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group>
                <Button
                  variant="link"
                  className="setup-add-link me-3"
                  onClick={() => {
                    console.log('addNote');
                  }}
                >
                  +<span className="underline ms-1">Add notes</span>
                </Button>
              </Form.Group>
            </Col>
          </Row>
          <ContactPerson contacts={projectMetadataRef.current!.contacts!}></ContactPerson>
          <Row className="mt-4 mb-5 text-center">
            <Col>
              <Button className="outlined-btn me-3" type="button" onClick={() => console.log('discard')}>
                Discard
              </Button>
              <Button className="filled-btn" type="submit" onClick={() => submit()}>
                Save
              </Button>
            </Col>
          </Row>
        </Form>
      </Container>
    </>
  );
};
