import { useQuery } from '@tanstack/react-query';
import { GET_USER_COMPANY } from 'constants/queryKeys';
import { useSpinnerEffect } from 'hooks/useSpinner';
import { cotService } from 'services/cot.service';
import { CotCompany, CotDevice, CotProject } from 'types/cot.types';
import { Company, Project, ProjectMetadata } from '../GWSetupClinicSearch';

const createCompany = (cotCompany: CotCompany): Company => {
  return { ...cotCompany, companies: [], projects: [] };
};

const createProject = async (cotProject: CotProject, cotDevice: CotDevice | undefined): Promise<Project> => {
  const metadata = await cotService.getProjectProperty<ProjectMetadata>(cotProject.company, cotProject.id, 'meta.data');
  return { metadata, belongToDevice: cotDevice?.project === cotProject.id, ...cotProject };
};

const populate = (company: Company, companies: Company[]) => {
  company.companies = companies.filter((subCompany) => subCompany.parent === company.id);
  company.companies.forEach((subCompany) => populate(subCompany, companies));
};

const getFlatProjects = async (companyIds: string[], deviceId: string): Promise<Project[]> => {
  const device = await cotService.getDeviceById(deviceId);
  const projects = await Promise.all(companyIds.map((companyId: string) => cotService.getProjects(companyId)));

  return Promise.all(
    projects.flatMap((project) => project).map(async (cotProject) => await createProject(cotProject, device))
  );
};

const getUserCompany = async (companyId: string, deviceId: string): Promise<Company> => {
  const subCompanies = await cotService.getSubCompanies(companyId);
  const companyName = await cotService.getCompanyName(companyId);
  const userCompany: Company = { name: companyName, id: companyId, parent: '', companies: [], projects: [] };
  const companies = [userCompany];
  if (subCompanies?.length) {
    const subCompaniesCompanies = subCompanies.map(createCompany);
    companies.push(...subCompaniesCompanies);
  }

  const companyIds = companies.map((c) => c.id);
  populate(userCompany, companies);

  const flatProjects = await getFlatProjects(companyIds, deviceId);

  companies.forEach((company) => (company.projects = flatProjects.filter((project) => project.company === company.id)));
  return userCompany;
};

export const useGetUserCompanyQuery = (companyId: string, deviceId: string): Company | undefined => {
  const { data: rootCompany, isLoading } = useQuery([GET_USER_COMPANY], () => getUserCompany(companyId, deviceId), {});
  useSpinnerEffect(isLoading);
  return rootCompany;
};
