import userIcon from 'assets/images/user_icon.svg';
import { useAppDispatch, useAppSelector } from 'hooks/useApp';
import { useAsync } from 'hooks/useAsync';
import { useState } from 'react';
import { Button, Offcanvas } from 'react-bootstrap';
import { cotService } from 'services/cot.service';
import { logoutTheUser, selectUser } from 'store/slices/userSlice';

export const UserIcon = () => {
  const dispatch = useAppDispatch();
  const [show, setShow] = useState(false);
  const user = useAppSelector(selectUser);
  const userLoggedIn: boolean = user.email !== undefined && user.email.length > 0;
  const { value: companyName } = useAsync(() => cotService.getCompanyName(user.company), {immediate: userLoggedIn});

  if (!userLoggedIn) {
    return;
  }

  return (
    <>
      <img src={userIcon} alt="" onClick={() => setShow((prev) => !prev)}></img>
      <Offcanvas show={show} onHide={() => setShow(false)} placement="top">
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>{user.email}</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          Company: {companyName}
          <Button
            className={'nb-submit-button rounded-pill ps-5 pe-5 pt-2 pb-2'}
            type="submit"
            onClick={() => {
              dispatch(logoutTheUser());
              setShow((prev) => !prev);
            }}
          >
            Logout
          </Button>
        </Offcanvas.Body>
      </Offcanvas>
    </>
  );
};
