
import { ProjectMetadata } from 'pages/GWSetupWizard';
import { Button, Col, Row } from 'react-bootstrap';
import { CotProject } from 'types/cot.types';
import { concatAddress } from 'utils/concatAddress';
import iTooltip from 'assets/images/i_tooltip.svg';

export const ClinicDetails = ({ projectMetadata, project }: { projectMetadata?: ProjectMetadata; project?: CotProject }) => {
    return (
      <>
        <Row>
          <Col className="d-flex">
            <div className="fs-30 flex-grow-1">{project?.name}</div>
            <Button variant="link">
              <img alt="action" src={iTooltip} />
            </Button>
          </Col>
        </Row>
        <Row>
          <Col>{concatAddress(projectMetadata?.address)}</Col>
        </Row>
        <Row>
          <Col className="d-flex">
            <span className="setup-label">Phone</span>
            {projectMetadata?.contact!.phone}
          </Col>
        </Row>
        <Row>
          <Col className="d-flex">
            <span className="setup-label">Mobile</span>
            {projectMetadata?.contact!.phone}
          </Col>
        </Row>
        {projectMetadata?.note ? (
          <Row>
            <Col className="d-flex">{`* ${projectMetadata?.note}`}</Col>
          </Row>
        ) : (
          <></>
        )}
      </>
    );
  };