import { useMutation, useQueryClient } from '@tanstack/react-query';
import { GET_USER_COMPANY } from 'constants/queryKeys';
import { useAppSelector } from 'hooks/useApp';
import { useShowPopup } from 'hooks/useShowPopup';
import { cotService } from 'services/cot.service';
import { selectUser } from 'store';
import { Company, Project } from '../GWSetupClinicSearch';
import { useGetUserCompanyQuery } from './useGetUserCompanyQuery';

export const INVENTORY = 'inventory';

class DeviceAllocationLogic {
  getFlatProjectsFromCompany = (company: Company | undefined): Project[] => {
    if (!company) {
      return [];
    }
    const projects = company.projects;
    return projects.concat(company.companies.flatMap(this.getFlatProjectsFromCompany));
  };

  getDeallocateDeviceFunction = (deviceId: string) => {
    const showPopup = useShowPopup();
    const queryClient = useQueryClient();
    const { mutate: deallocateDevice } = useMutation(
      (project: Project) => cotService.deallocateDevice(project.company, project.id, deviceId),
      {
        onSuccess: () => {
          queryClient.invalidateQueries([GET_USER_COMPANY]);
          showPopup('Deallocate successfully!');
        },
        onError: (error, variables, context) => {
          showPopup('Failed to deallocate!');
          console.error(error);
        },
      }
    );
    return deallocateDevice;
  };

  useDeviceAllocationLogic = (deviceId: string) => {
    const showPopup = useShowPopup();
    const user = useAppSelector(selectUser);
    const rootCompany = useGetUserCompanyQuery(user.company, deviceId);

    const flatProjects = this.getFlatProjectsFromCompany(rootCompany);
    const deviceProject = flatProjects.find((project) => project.belongToDevice);
    const isAllocated = deviceProject !== undefined && deviceProject.name.toLowerCase() !== INVENTORY;

    const deallocateDevice = this.getDeallocateDeviceFunction(deviceId);
    const deallocate = () => {
      const inventory = flatProjects?.find((project) => project.name.toLowerCase() === INVENTORY);
      if (!inventory) {
        showPopup('Inventory not found!');
        return;
      }
      deallocateDevice(inventory);
    };

    return { isAllocated, rootCompany, flatProjects, deallocate };
  };
}

export const deviceAllocationLogic = new DeviceAllocationLogic();
