import gwDeviceIcon from 'assets/images/gw_device.svg';
import { Stepper } from 'components/Stepper/Stepper';
import { Button, Container } from 'react-bootstrap';
import { Link, useParams } from 'react-router-dom';
import { routsConstant } from 'routes/routes.const';
import '../GWSetupWizard.css';

export const GWSetupStart = () => {
  const { deviceId } = useParams();
  return (
    <>
      <Container className="text-center">
        <div className="mb-2">Installation wizard</div>
        <div className="setup-step-title">Gateway</div>
        <div className="setup-step-title mb-5">{deviceId}</div>
        <div className="mb-5">
          <img src={gwDeviceIcon} alt=""></img>
        </div>
        <Stepper items={['Clinic', 'Location', 'Machines']}></Stepper>
        <div className="fs-20 mb-3">Setup the gateway in 3 simple steps</div>
        <div className="fs-14 ms-5 me-5 mb-5">
          Make sure the gateway is connected to the clinic's secure WiFi network, and that all relevant machines are on.
        </div>
        <Link to={routsConstant.gwSetupClinicSearch.relatirve({ deviceId: deviceId! })}>
          <Button className="filled-btn ps-5 pe-5 pt-2 pb-2 mb-3">Let's start</Button>
        </Link>
        <div className="link">Skip wizard</div>
      </Container>
    </>
  );
};
