import nav_tutt_logo from 'assets/images/nav_tutt_logo.svg';
import { Container, Nav, Navbar } from 'react-bootstrap';
import { GearIcon } from './components/GearIcon/GearIcon';
import { UserIcon } from './components/UserIcon/UserIcon';
import { WifiSignalIcon } from './components/WifiSignalIcon/WifiSignalIcon';
import './NBNavbar.css';

const NavIcons = () => {
  const icons: Array<JSX.Element | undefined> = [UserIcon(), WifiSignalIcon(), GearIcon()];
  return (
    <>
      {icons
        .filter((icon) => icon)
        .map((icon, indx) => (
          <div key={indx} className="p-1">
            {icon}
          </div>
        ))}
    </>
  );
};

export const NBNavBar = () => {
  const cl = `navbar shadow-sm`;
  return (
    <Navbar className={cl} sticky="top">
      <Container>
        <Nav>
          <img src={nav_tutt_logo} alt=""></img>
        </Nav>
        <Nav>
          <NavIcons />
        </Nav>
      </Container>
    </Navbar>
  );
};
