import { useState, useContext, createContext } from 'react'
import { WifiDetails } from './model/WfiDetails';
import { WifiLayout } from '../../layouts/WifiLayout';

type WifiContextArgs = {
    wfiDetails: WifiDetails;
    showWifiSelectModal: boolean;
    setShowWifiSelectModal: (show: boolean) => void;
    setWfiDetails: (wfiDetails: WifiDetails) => void;
}

const initialState: WifiContextArgs = {
    wfiDetails: { signal: 0, ssid: "", connected: false },
    showWifiSelectModal: false,
    setShowWifiSelectModal: (show: boolean) => { },
    setWfiDetails: (wfiDetails: WifiDetails) => { },
};

const WifiContext = createContext<WifiContextArgs>(initialState);

export const useWifiContext = () => {
    return useContext(WifiContext);
}

export const WifiProvider = () => {
    const [wfiDetails, setWfiDetails] = useState(initialState.wfiDetails);
    const [showWifiSelectModal, setShowWifiSelectModal] = useState(false);

    const args = {wfiDetails, showWifiSelectModal, setShowWifiSelectModal, setWfiDetails};

    return <>
        <WifiContext.Provider value={args}>
            <WifiLayout></WifiLayout>
        </WifiContext.Provider>
    </>
}