import arrowIcon from 'assets/images/arrow.svg';
import { ClinicDetails } from 'components/ClinicDetails/ClinicDetails';
import { ContactList } from 'components/ContactList/ContactList';
import { useGetProjectData } from 'hooks/project/useGetProjectData';
import { useAsync } from 'hooks/useAsync';
import { useSpinnerEffect } from 'hooks/useSpinner';
import { useShowPopup } from 'hooks/useShowPopup';
import { useEffect } from 'react';
import { Button, Col, Container, Form, Row } from 'react-bootstrap';
import { useNavigate, useParams } from 'react-router-dom';
import { routsConstant } from 'routes/routes.const';
import { cotService } from 'services/cot.service';
import { GWSetupProgressBar } from '../components/GWSetupProgressBar/GWSetupProgressBar';
import { GWSetupWizardSteps } from '../types/GWSetupWizard.types';

const useAllocateDevice = (companyId: string, projectId: string, deviceId: string) => {
  const { status: allocateStatus, execute: allocateDevice } = useAsync<void>(
    () => cotService.allocateDevice(companyId!, projectId!, deviceId!),
    { immediate: false }
  );
  useSpinnerEffect(allocateStatus === 'pending');
  const showPopup = useShowPopup();
  const navigate = useNavigate();
  useEffect(() => {
    if (allocateStatus === 'success') {
      navigate(
        routsConstant.gwSetupMachinesRecommendation.relatirve({
          deviceId: deviceId!,
        })
      );
    }
    if (allocateStatus === 'error') {
      showPopup('Failed to allocate!');
    }
  }, [allocateStatus, deviceId, navigate, showPopup]);

  return allocateDevice;
};

export const GWSetupLocationDetails = () => {
  const { projectId, companyId, deviceId } = useParams();
  const { projectMetadata, project } = useGetProjectData(companyId!, projectId!);
  const navigate = useNavigate();
  const allocateDevice = useAllocateDevice(companyId!, projectId!, deviceId!);
  const gwLocationDetails = {
    floor: '6th',
    room: '168a',
    phone: '0131 496 0128',
  };

  return (
    <>
      <GWSetupProgressBar currentStep={GWSetupWizardSteps.location} />
      <Container>
        <Form>
          <Row>
            <Col>
              <Button className="circle-button" onClick={() => console.log('back')}>
                <img alt="back" src={arrowIcon} />
              </Button>
            </Col>
          </Row>
          <Row>
            <Col className="setup-step-title mb-4">Gateway Location</Col>
          </Row>
          <ClinicDetails projectMetadata={projectMetadata} project={project}></ClinicDetails>
          <Row>
            <Col className="setup-section-title font-weight-bold fs-20 mt-4">Gateway Info</Col>
          </Row>
          <Row>
            <Col className="d-flex">
              <span className="setup-label">Floor</span>
              {gwLocationDetails.floor}
            </Col>
            <Col className="d-flex">
              <span className="setup-label">Room</span>
              {gwLocationDetails.room}
            </Col>
          </Row>
          <Row>
            <Col className="d-flex">
              <span className="setup-label">Phone</span>
              {gwLocationDetails.phone}
            </Col>
          </Row>
          <Row>
            <Col className="setup-section-title font-weight-bold fs-20 mt-4">Contacts</Col>
          </Row>
          <ContactList contacts={projectMetadata?.contacts ?? []}></ContactList>
          <Row className="mt-4 mb-5 text-center">
            <Col>
              <Button
                className="outlined-btn me-3"
                type="button"
                onClick={() =>
                  navigate(
                    routsConstant.gwSetupSetLocation.relatirve({
                      companyId: companyId!,
                      projectId: projectId!,
                      deviceId: deviceId!,
                    })
                  )
                }
              >
                Edit
              </Button>
              <Button className="filled-btn" type="submit" onClick={() => allocateDevice()}>
                Approve
              </Button>
            </Col>
          </Row>
        </Form>
      </Container>
    </>
  );
};
