import gwDeviceSmileIcon from 'assets/images/gw_device_smile.svg';
import { Button, Col, Container, Row } from 'react-bootstrap';
import { GWSetupProgressBar } from '../components/GWSetupProgressBar/GWSetupProgressBar';
import '../GWSetupWizard.css';
import { ConnectionType, GWSetupWizardSteps } from '../types/GWSetupWizard.types';

export const GWSetupCompleted = () => {
  const gatewayId = '12345678';
  const selectedClinic = {
    name: 'Cavity Free',
    address: '41 North Road KIRKCALDY KY28 6XP',
    phone: '0131 496 0120',
    mobile: '0151 496 0260',
  };

  const gwLocationDetails = {
    floor: '6th',
    room: '168a',
    phone: '0131 496 0128',
  };

  const contacts = [
    {
      name: 'Mike Wazowski',
      email: 'mikew@cavityfree.co.uk',
      title: 'Head of IT',
      phone: '0131 496 5157',
    },
    {
      name: 'Randall Boggs',
      email: 'randallb@cavityfree.co.uk',
      title: 'Chief Engineer',
      phone: '0131 496 6668',
    },
  ];

  const machines = [
    {
      name: 'machineA',
      serialNo: '1234456',
      model: 'modelA',
      connectionType: ConnectionType.serialPort,
      portNo: 111,
    },
    {
      name: 'machineB',
      serialNo: '88866555',
      model: 'modelB',
      connectionType: ConnectionType.serialPortConnection,
      portNo: 333,
    },
    {
      name: 'machineC',
      serialNo: '34234232',
      model: 'modelC',
      connectionType: ConnectionType.lanConnection,
    },
  ];

  const getMachineTypeDesc = (machine: any) => {
    switch (machine.connectionType) {
      case ConnectionType.serialPort:
        return `Port ${machine.portNo}`;
      case ConnectionType.lanConnection:
        return `LAN`;
      default:
        break;
    }
  };

  return (
    <>
      <GWSetupProgressBar currentStep={GWSetupWizardSteps.completed} />
      <Container>
        <Row>
          <Col className="text-center setup-section-title font-weight-normal mb-5 margin-top-70">Ready for go!</Col>
        </Row>
        <Row>
          <Col className="text-center mb-5">
            <img src={gwDeviceSmileIcon} alt=""></img>
          </Col>
        </Row>
        <Row>
          <Col className="text-center setup-step-title mb-5 font-weight-bold">Gateway {gatewayId}</Col>
        </Row>
        <Row>
          <Col className="text-center fs-30">{selectedClinic.name}</Col>
        </Row>
        <Row>
          <Col className="text-center fs-20">{selectedClinic.address}</Col>
        </Row>
        <Row>
          <Col className="text-center d-flex justify-content-center mb-3">
            <div className="d-flex flex-column me-3">
              <div className="fs-20">{gwLocationDetails.floor}</div>
              <div className="setup-bold-label-14">Floor</div>
            </div>
            <div className="dd-flex flex-column ms-3">
              <div className="fs-20">{gwLocationDetails.room}</div>
              <div className="setup-bold-label-14">Room</div>
            </div>
          </Col>
        </Row>
        <Row>
          <Col className="text-center setup-bold-label-14">Contacts</Col>
        </Row>
        {contacts.map((contact) => (
          <Row>
            <Col className="text-center fs-20">{contact.name}</Col>
          </Row>
        ))}
        <Row>
          <Col className="text-center setup-section-title underline mt-5">Connected Devices</Col>
        </Row>
        {machines.map((machine) => (
          <Row className="h-70 mb-2">
            <Col className="d-flex align-items-center">
              <div className="machine-icon"></div>
              <div className="d-flex flex-column flex-grow-1">
                <div className="setup-section-title">{machine.name}</div>
                <div className="setup-section-subtitle fs-14">
                  {machine.serialNo}&nbsp;|&nbsp;{machine.model}
                </div>
              </div>
              <div className="fs-21 width-100 text-left">{getMachineTypeDesc(machine)}</div>
            </Col>
          </Row>
        ))}
        <Row className="mt-5 mb-5 text-center">
          <Col>
            <Button className="outlined-btn me-3" type="button" onClick={() => console.log('edit')}>
              Edit
            </Button>
            <Button className="filled-btn" type="submit" onClick={() => console.log('done')}>
              Done
            </Button>
          </Col>
        </Row>
      </Container>
    </>
  );
};
