import search from 'assets/images/search.svg';
import { InputText } from 'components/InputText/InputText';
import { useState } from 'react';
import { Button, Card, Col, Container, Row } from 'react-bootstrap';
import { Link, useParams } from 'react-router-dom';
import { routsConstant } from 'routes/routes.const';
import { concatAddress } from 'utils/concatAddress';
import { GWSetupProgressBar } from '../components/GWSetupProgressBar/GWSetupProgressBar';
import '../GWSetupWizard.css';
import { GWSetupWizardSteps } from '../types/GWSetupWizard.types';
import { deviceAllocationLogic, INVENTORY } from './utils/GWSetupClinicLogic';

export type Company = {
  name: string;
  id: string;
  parent: string;
  companies: Company[];
  projects: Project[];
};

export type Project = {
  name: string;
  id: string;
  description: string;
  company: string;
  belongToDevice: boolean;
  metadata?: ProjectMetadata;
};

export type ProjectMetadata = {
  address?: ProjectAddress;
  note: string;
  contact?: ProjectContact;
  contacts?: Array<ProjectContact>;
};

export type ProjectContact = {
  name: string;
  position: string;
  phone: string;
  email: string;
};

export type ProjectAddress = {
  line1: string;
  line2: string;
};

const ProjectComponent = (project: Project) => {
  const { deviceId } = useParams();

  return (
    <Link
      to={routsConstant.gwSetupSetLocation.relatirve({
        companyId: project.company,
        projectId: project.id,
        deviceId: deviceId!,
      })}
      style={{ textDecoration: 'none' }}
    >
      <div className="setup-section-title">{project.name}</div>
      <div className="setup-section-subtitle">{concatAddress(project.metadata?.address)}</div>
      <hr />
    </Link>
  );
};

const CompanyComponent = (company: Company) => {
  return (
    <>
      <Card>
        <Card.Title>{company.name}</Card.Title>
        <Card.Body>
          {company.companies.map((subCompany) => (
            <CompanyComponent key={subCompany.id} {...subCompany} />
          ))}
          {company.projects.map((project) => (
            <ProjectComponent key={project.id} {...project} />
          ))}
        </Card.Body>
      </Card>
    </>
  );
};

export const GWSetupClinicSearch = () => {
  const { deviceId } = useParams();
  const { isAllocated, rootCompany, flatProjects, deallocate } = deviceAllocationLogic.useDeviceAllocationLogic(
    deviceId!
  );
  const [queryFilter, setQueryFilter] = useState('');

  if (!rootCompany) {
    return <></>;
  }
  if (isAllocated) {
    return (
      <>
        <Container>
          <Button variant="primary" onClick={() => deallocate()}>
            Deallocate
          </Button>
        </Container>
      </>
    );
  }
  return (
    <>
      <GWSetupProgressBar currentStep={GWSetupWizardSteps.clinic} />
      <Container>
        <Row>
          <Col className="setup-step-title">Assign to a Clinic</Col>
        </Row>
        <Row className="pb-5 align-items-center">
          <Col>
            <InputText
              placeholder="Search"
              icon={search}
              onChange={(e) => {
                setQueryFilter(e.target.value);
              }}
            />
          </Col>
          <Col>
            <Button className={'nb-submit-button rounded-pill ps-3 pe-3 pt-2 pb-2'} type="submit">
              Add New
            </Button>
          </Col>
        </Row>
        <Row>
          <Col>
            {flatProjects
              .filter((project) => project.name.toLowerCase().includes(queryFilter.toLowerCase()))
              .filter((project) => project.name.toLowerCase() !== INVENTORY)
              .map((project) => (
                <ProjectComponent key={project.id} {...project}></ProjectComponent>
              ))}
          </Col>
        </Row>
      </Container>
    </>
  );
};
