import arrowIcon from 'assets/images/arrow.svg';
import addressMarker from 'assets/images/address-marker.svg';
import { Button, Col, Container, Form, InputGroup, Row } from 'react-bootstrap';
import { GWSetupWizardSteps } from '../types/GWSetupWizard.types';
import { GWSetupProgressBar } from '../components/GWSetupProgressBar/GWSetupProgressBar';

export const GWSetupNewClinic = () => {
  return (
    <>
      <GWSetupProgressBar currentStep={GWSetupWizardSteps.clinic} />
      <Container>
        <Form>
          <Row>
            <Col>
              <Button className="circle-button" onClick={() => console.log('back')}>
                <img alt="back" src={arrowIcon} />
              </Button>
            </Col>
          </Row>
          <Row>
            <Col className="setup-step-title mb-4">Create New Clinic</Col>
          </Row>
          <Row>
            <Col className="setup-section-title">Clinic Info</Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="clinicName">
                <Form.Control className="setup-form-field-text" type="text" placeholder="Name*" />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="clinicAddress">
                <InputGroup>
                  <Form.Control className="setup-form-field-text" type="text" placeholder="Address*" />
                  <InputGroup.Text className="address-marker">
                    <img alt="address" src={addressMarker} />
                  </InputGroup.Text>
                </InputGroup>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="clinicFloor">
                <Form.Control className="setup-form-field-text" type="text" placeholder="Floor*" />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="clinicPhone">
                <InputGroup>
                  <Form.Control className="setup-form-field-text" type="text" placeholder="Phone*" />
                  <InputGroup.Text className="plus-icon">
                    <img alt="add phone" src={arrowIcon} />
                  </InputGroup.Text>
                </InputGroup>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group controlId="clinicEmail">
                <InputGroup>
                  <Form.Control className="setup-form-field-text" type="text" placeholder="Email*" />
                  <InputGroup.Text className="plus-icon">
                    <img alt="add email" src={arrowIcon} />
                  </InputGroup.Text>
                </InputGroup>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group>
                <Button
                  variant="link"
                  className="setup-add-link me-3"
                  onClick={() => {
                    console.log('addNote');
                  }}
                >
                  +<span className="underline ms-1">Add notes</span>
                </Button>
                <Button
                  variant="link"
                  className="setup-add-link"
                  onClick={() => {
                    console.log('addWorkingHours');
                  }}
                >
                  +<span className="underline ms-1">Add working hours</span>
                </Button>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col className="setup-section-title mt-4">Contact Person</Col>
          </Row>
          <Row>
            <Col className="setup-comment mb-2">
              We will use this info to create a CoT user, please make sure it's accurate.
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="contactName">
                <Form.Control className="setup-form-field-text" type="text" placeholder="Name*" />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="contactEmail">
                <Form.Control className="setup-form-field-text" type="text" placeholder="Email*" />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="contactTitle">
                <Form.Control className="setup-form-field-text" type="text" placeholder="Title" />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group controlId="phone">
                <InputGroup>
                  <Form.Control className="setup-form-field-text" type="text" placeholder="Phone" />
                  <InputGroup.Text className="plus-icon">
                    <img alt="add phone" src={arrowIcon} />
                  </InputGroup.Text>
                </InputGroup>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group controlId="addContact">
                <Button
                  variant="link"
                  className="setup-add-link"
                  onClick={() => {
                    console.log('addcontact');
                  }}
                >
                  +<span className="underline ms-1">Add another contact</span>
                </Button>
              </Form.Group>
            </Col>
          </Row>
          <Row className="mt-4 mb-5 text-center">
            <Col>
              <Button className="outlined-btn me-3" type="button" onClick={() => console.log('discard')}>
                Discard
              </Button>
              <Button className="filled-btn" type="submit" onClick={() => console.log('Save')}>
                Save
              </Button>
            </Col>
          </Row>
        </Form>
      </Container>
    </>
  );
};
