import { Nav } from "react-bootstrap"
import { useTranslation } from "react-i18next";
import { Link, Outlet } from "react-router-dom"

export const OperatorGuideLayout = () => {
    const { t } = useTranslation();

    return <>
        <Nav fill variant="tabs" defaultActiveKey="/home">
            <Nav.Item>
                <Nav.Link eventKey="link-0"><Link to="/operator/hotspot">{t("hotspotDetailsNav")}</Link></Nav.Link>
            </Nav.Item>
            <Nav.Item>
                <Nav.Link eventKey="link-1"><Link to="/operator/homepage">{t("homepageDetailsNav")}</Link></Nav.Link>
            </Nav.Item>
        </Nav>
        <Outlet></Outlet>
    </>
}