import { useEffect, useState } from 'react';
import { Form } from 'react-bootstrap';
import { cotService } from 'services/cot.service';
import { useAppSelector } from '../../hooks/useApp';
import { selectUser, UserState } from '../../store/slices/userSlice';

export const UserDetails = () => {
  const user: UserState = useAppSelector(selectUser);
  const [companyName, setCompanyName] = useState('');

  useEffect(() => {
    const loadData = async () => {
      if (user.apiKey === '') {
        return;
      }
      setCompanyName(await cotService.getCompanyName(user.company));
    };
    loadData();
  }, [user.apiKey, user.company]);

  if (user.apiKey === '') {
    return <></>;
  }

  return (
    <>
      <Form.Label>
        {user.email}, {companyName}
      </Form.Label>
    </>
  );
};
